.laoding {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: #fff;
    z-index: 99;
}

.loading-style-4 {
    width: 1.33333rem;
    height: 1.33333rem;
    position: fixed;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 99999;
}

.loading-style-4 span {
    display: inline-block;
    width: .266667rem;
    height: .266667rem;
    border-radius: 50%;
    background: lightgreen;
    position: absolute;
    -webkit-animation: loading-style-4 1.04s ease-in-out infinite;
    animation: loading-style-4 1.04s ease-in-out infinite;
}

@-webkit-keyframes loading-style-4 {
    0% {
        -webkit-transform: scale(1.2);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(.3);
        opacity: 0.5;
    }
}

@keyframes loading-style-4 {
    0% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(.3);
                transform: scale(.3);
        opacity: 0.5;
    }
}

.loading-style-4 span:nth-child(1) {
    left: 0;
    top: 50%;
    margin-top: -10px;
    -webkit-animation-delay: 0.13s;
    animation-delay: 0.13s;
}

.loading-style-4 span:nth-child(2) {
    left: 14px;
    top: 14px;
    -webkit-animation-delay: 0.26s;
    animation-delay: 0.26s;
}

.loading-style-4 span:nth-child(3) {
    left: 50%;
    top: 0;
    margin-left: -10px;
    -webkit-animation-delay: 0.39s;
    animation-delay: 0.39s;
}

.loading-style-4 span:nth-child(4) {
    top: 14px;
    right: 14px;
    -webkit-animation-delay: 0.52s;
    animation-delay: 0.52s;
}

.loading-style-4 span:nth-child(5) {
    right: 0;
    top: 50%;
    margin-top: -10px;
    -webkit-animation-delay: 0.65s;
    animation-delay: 0.65s;
}

.loading-style-4 span:nth-child(6) {
    right: 14px;
    bottom: 14px;
    -webkit-animation-delay: 0.78s;
    animation-delay: 0.78s;
}

.loading-style-4 span:nth-child(7) {
    bottom: 0;
    left: 50%;
    margin-left: -10px;
    -webkit-animation-delay: 0.91s;
    animation-delay: 0.91s;
}

.loading-style-4 span:nth-child(8) {
    bottom: 14px;
    left: 14px;
    -webkit-animation-delay: 1.04s;
    animation-delay: 1.04s;
}


.modal {
    width:100%;height: 100%;position: fixed;left:0;top: 0;overflow: hidden;
    z-index:99999;background: rgba(0,0,0,.5);
}
.modal .icon {
    width:.6rem;
    height:.6rem;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
}

.modal button {
    background: transparent;
    border: none;
    outline: none;
    cursor: pointer;
}

.modal .dialog {
    background: #FFFFFF;
    min-width: 4.5rem;
    padding: .3rem .2rem;
    text-align: center;
    border-radius: .1rem;
    box-shadow: 0rem .1rem .3rem 0rem rgba(0, 0, 0, .1);
    position: fixed;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%)
}

.modal .dialog>h4 {
    color: #454545;
    text-align: center;
    margin: 0;
    font-size: .4rem;
    font-weight:normal
}

.modal .dialog .button-wrapper {
    padding: .3rem .6rem 0;
    display: flex;
    justify-content: space-around;
}

.modal .dialog .button-wrapper>button>svg {
    font-size: .6rem;
}

.modal .dialog .button-wrapper .check>svg {
    color: #50C881;
}

.modal .dialog .button-wrapper .close>svg {
    color: #FF5152;
}

.modal-alert>button {
    font-size: .6rem;
    color: #50C881;
    margin-top: .3rem;
}

.modal-prompt>input[type=text] {
    font-size: .2rem;
    margin: .2rem .5rem 0;
    padding: .1rem .3rem;
    border: none;
    outline: none;
    background: rgba(0, 0, 0, .02);
    border-radius: .1rem;
}

.dialog-wrapper-enter {
    opacity: 0.01;
    -webkit-transform: translateY(-30%);
            transform: translateY(-30%);
}

.dialog-wrapper-enter-active {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: all 500ms;
    transition: all 500ms;
}

.dialog-wrapper-exit {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.dialog-wrapper-exit-active {
    opacity: 0.01;
    -webkit-transform: translateY(-30%);
            transform: translateY(-30%);
    -webkit-transition: all 500ms;
    transition: all 500ms;
}

.modal-wrapper-enter {
    background: rgba(0, 0, 0, 0);
}

.modal-wrapper-enter-active {
    background: rgba(0, 0, 0, .4);
    -webkit-transition: all 500ms ease-out;
    transition: all 500ms ease-out;
}

.modal-wrapper-exit {
    background: rgba(0, 0, 0, .4);
}

.modal-wrapper-exit-active {
    background: rgba(0, 0, 0, 0);
    -webkit-transition: all 500ms ease-out;
    transition: all 500ms ease-out;
}
